<!--
    Component item header tab view
-->
<template>
  <div :class="classTab()" :id="'tabHeader' + tab.index">
    <div class="TabViewHeaderItemComponentTitle" v-on:click="onClickTitle">{{tab.title}}</div>
    <div class="TabViewHeaderItemComponentClose" v-on:click="onClickClose">
      <i class="far fa-times-circle"></i>
    </div>
  </div>
</template>
<script>
import BaseComponent from "../base/BaseComponent";
import VueContext from "../../context/VueContext";

export default {
  extends: BaseComponent,
  name: "TabViewHeaderItemComponent",
  props: {
    first: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    /**
     * Method to know tab is active
     * @returns true if active false if not
     */
    active: function() {
      return VueContext.activeTab === this.tab.index;
    },
    /**
     * Method to knwo class tab
     * @returns class for tab
     */
    classTab: function() {
      return (
        (this.first ? "TabViewHeaderItemComponentFirst" : "") +
        " TabViewHeaderItemComponent " +
        (VueContext.activeTab === this.tab.index
          ? "TabViewHeaderItemComponentActive"
          : "")
      );
    },
    /**
     * Method active tab
     */
    onClickTitle: function() {
      VueContext.changeTab(this.tab);
    },
    /**
     * Method for close tab
     */
    onClickClose: function(){
      VueContext.closeTab(this.tab);
    }
  }
};
</script>
<style scoped>
.TabViewHeaderItemComponent {
  float: left;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  height: 28px;
  margin-top: 2px;
  text-align: center;
  color: var(--rf-tabFontColor, #466289);
  background-color: transparent;
  border-top: 1px solid transparent;
  border-left: 1px solid transparent;
  border-right: 1px solid transparent;
  border-bottom: 2px solid var(--rf-tabFontColor, #466289);
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
}

.TabViewHeaderItemComponentTitle {
  float: left;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  cursor: pointer;
}

.TabViewHeaderItemComponentClose {
  float: left;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  cursor: pointer;
  margin-left: 5px;
}

.TabViewHeaderItemComponentActive {
  height: 28px;
  margin-top: 2px;
  font-weight: bold;
  background: var(--rf-tabBackColor, #ffb739);
  border-top: 1px solid var(--rf-tabBackColor, #ffb739);
  border-left: 1px solid var(--rf-tabBackColor, #ffb739);
  border-right: 1px solid var(--rf-tabBackColor, #ffb739);
  border-bottom: 2px solid var(--rf-tabBackColor, #ffb739);
  border-top-right-radius: 0.5em 1em;
}

.TabViewHeaderItemComponentFirst {
  margin-left: 10px;
}
</style>